import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';

export const routes: Routes = [
	{
		path: '',
		children: [
		
			{ path: 'eventcenter-activite', loadChildren: './eventcenter-activite/eventcenter-activite.module#EventcenterActiviteModule' },//事件中心
		
		]
	}

]

export const EventcenterRouting: ModuleWithProviders = RouterModule.forChild(routes);
